#principles {
	padding: 6.2rem 3.4rem;
	@media screen and (min-width: 62.5em) {
		padding: 6.2rem 7rem; }
	.section__title {
		width: min-content;
		@media screen and (min-width: 31.25em) {
			width: max-content;
			margin-bottom: 7.5rem; }
		&::after {
			background: url('../../img/principles__title.png') center no-repeat;
			background-size: cover; } }


	.container {
		@media screen and (min-width: 44.5em) {
			padding: 0 3.4rem; }

		&__row {
			@media screen and (min-width: 47.5em) {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				width: 100%;
				max-width: 66rem; }
			@media screen and (min-width: 62.5em) {
				max-width: 71rem; }
			@media screen and (min-width: 68.75em) {
				max-width: 100%;
				justify-content: center; } } } }


.principle {
	margin-bottom: 6rem;
	max-width: 28.1rem;
	@media screen and (min-width: 68.75em) {
		max-width: 33.1rem; }
	@media screen and (min-width: 75.75em) {
		max-width: 44.1rem; }
	&:first-child {
		@media screen and (min-width: 68.75em) {
			margin-right: 17.7rem; } }
	&__tag, &__moto {
		@include f-bold;
		font-weight: bold; }
	&__tag {
		font-size: 2.4rem;
		color: $gray;
		margin-bottom: 1.3rem;
		@media screen and (min-width: 68.75em) {
			font-size: 3.4rem; }
		@media screen and (min-width: 75.75em) {
			font-size: 4rem; } }
	&__moto {
		font-size: 5rem;
		margin-bottom: 1.2rem;
		@media screen and (min-width: 68.75em) {
			font-size: 7rem; }
		@media screen and (min-width: 75.75em) {
			font-size: 10rem;
			line-height: 13rem; } }

	&__descr {
		font-size: 1.6rem;
		line-height: 3.5rem;
		letter-spacing: 0.03em;
		@media screen and (min-width: 68.75em) {
			font-size: 1.9rem; }
		@media screen and (min-width: 75.75em) {
			font-size: 2.4rem;
			line-height: 4.5rem; } } }
